export const GalleryConfigMixin = {
    data() {
        return {
            config: {
                isSearchActive: false,
                isFiltersActive: true,
                isMapActive: false,
                isLoginActive: false,
                isBasketActive: false,
                isBasketActiveForNonLoginUsers: false,
            },
        };
    },
    computed: {
        isHeaderShown() {
            const {isSearchActive, isFiltersActive, isLoginActive, isBasketActive, isBasketActiveForNonLoginUsers} = this.config;

            return isSearchActive
                || isFiltersActive
                || isLoginActive
                || isBasketActive
                || isBasketActiveForNonLoginUsers;
        },
        isBasketShown() {
            const {isBasketActive, isBasketActiveForNonLoginUsers} = this.config;

            return isBasketActive && (isBasketActiveForNonLoginUsers || this.userIsLoggedIn);
        }
    },
    methods: {
        setGalleryConfig({
                             isSearchActive,
                             isFiltersActive,
                             isMapActive,
                             isLoginActive,
                             isBasketActive,
                             isBasketActiveForNonLoginUsers
                         }) {
            this.config.isSearchActive = isSearchActive;
            this.config.isFiltersActive = isFiltersActive;
            this.config.isMapActive = isMapActive;
            this.config.isLoginActive = isLoginActive;
            this.config.isBasketActive = isBasketActive;
            this.config.isBasketActiveForNonLoginUsers = isBasketActiveForNonLoginUsers;
        }
    }

}